/**
 * form
 */
export default ({ fetchMock, delay, mock, toSuccess, toError }) => {
  // 
  return {
    '/api/form/autoComplete': options => {
      const body = JSON.parse(options.body);
      const userName = body;

      return toSuccess(
        mock({
          'list|3-10': [{
            'id': '@id',
            'name': userName + '@cword("", 1, 2)', // 
            'age|1-100': 100,                   // 100
            'birthday': '@date("yyyy-MM-dd")',  // 
            'city': '@city(true)',              // 
            'phone': /^1[385][1-9]\d{8}/,       // 
            'content': '@csentence',
          }]
        }),
        400
      );
    }
  };
};
