import React from 'react';
import PageLoading from 'components/Loading/PageLoading';
import { normal } from 'components/Notification';
import store from 'cmn-utils/lib/store';

// , normalantdNotice
const notice = normal;

/**
 *  
 */
export default {
  /**
   * HTMLtitle
   */
  htmlTitle: 'DBAdmin - {title}',
  
  /**
   * 
   */
  notice,

  // 
  request: {
    prefix: '/api',

    // 
    withHeaders: () => ({
      token: store.getStore("token"),
    }),

    /**
     * modelEnhance
     * 
     * {status: true, data: ...} // 
     * {status: false, message: ...} // 
     * response
     * 
     */
    afterResponse: response => {
      const { status, message } = response;
      if (status) {
        return response;
      } else {
        throw new Error(message);
      }
    },
    errorHandle: err => {
      // 
      if (err.name === 'RequestError') {
        notice.error(err.text || err.message);
      }
    }
  },

  // 
  exception: {
    global: (err, dispatch) => {
      const errName = err.name;
      // RequestError
      if (errName === 'RequestError') {
        notice.error(err.message);
        console.error(err); 
      } else {
        console.error(err);
      }
    },
  },

  // 
  pageHelper: {
    // 
    requestFormat: pageInfo => {
      const { pageNum, pageSize, filters, sorts } = pageInfo;
      return {
        currentPage: pageNum,
        showCount: pageSize,
        sortMap: sorts,
        paramMap: filters
      };
    },

    // 
    responseFormat: resp => {
      const {
        currentPage,
        showCount,
        totalResult,
        dataList,
        totalPage
      } = resp.data;
      return {
        pageNum: currentPage,
        pageSize: showCount,
        total: totalResult,
        totalPages: totalPage,
        list: dataList
      };
    }
  },

  // 
  router: {
    loading: <PageLoading loading />
  },

  /**
   * 
   * 
   * 
   * {
   *   status: true,
   *   data: responseData
   * }
   * 
   * {
   *   status: false,
   *   code: 500,
   *   message: ''
   * }
   *  orz...
   */
  mock: {
    toSuccess: response => ({
      status: true,
      data: response
    }),

    toError: message => ({
      status: false,
      message: message
    })
  }
};
