/**
 * 
 * @param {FetchMock} fetchMock fetchMock
 * @param {function} delay  ms : delay(mockData)  delay(mockData, 200)
 * @param {function} mock mock:

   mock({
     'string|1-10': '' // 110
   })

   // {'string': ''} 

   http://mockjs.com/examples.html
 */
export default ({fetchMock, delay, mock, toSuccess, toError}) => {
  // fetchMock
  // fetchMock.mock(/httpbin.org\/post/, {/* response */}, {/* options */});

  return {
    '/api/user/login': (options) => {
      if (options.body) {
        const user = JSON.parse(options.body);
        if (user.userName === 'admin' && user.password === 'admin') {
          return toSuccess(mock({
            'userName': 'admin',                // 
            'name': '@cname',                   // 
            'age|1-100': 100,                   // 100
            'birthday': '@date("yyyy-MM-dd")',  // 
            'city': '@city(true)',              // 
            'phone': /^1[385][1-9]\d{8}/,       // 
            'token': '@guid'                    // token
          }), 400);
        } else {
          return toError(' admin/admin');
        }
      } else {
        return toError('');
      }
    },
    '/api/user/register': options => toSuccess(),
    '/api/user/menu': options => toSuccess([
      {
        name: 'Dashboard',
        icon: 'DashboardOutlined',
        path: '/dashboard',
      },
      {
        name: 'Components',
        icon: 'DesktopOutlined',
        path: '/component',
        children: [
          {
            name: 'Toolbar',
            path: '/toolbar',
          },
          {
            name: 'BaseComponent',
            path: '/baseComponent',
          },
          {
            name: 'Columns',
            path: '/column',
          },
          {
            name: 'Searchbar',
            path: '/searchBar',
          },
          {
            name: 'Datatable',
            path: '/datatable',
          },
          {
            name: 'Forms',
            path: '/form',
          },
          {
            name: 'TransferTree',
            path: '/transferTree',
          },
          {
            name: 'Charts',
            path: '/charts',
            children: [
              {
                name: 'ECharts',
                path: '/charts/ec',
              },
              {
                name: 'G2',
                path: '/charts/g2',
              },
            ]
          },
          {
            name: 'Print',
            path: '/print',
          },
          {
            name: 'Banner',
            path: '/banner',
          },
        ],
      },
      {
        name: 'UI',
        icon: 'ShareAltOutlined',
        path: '/ui',
        children: [
          {
            name: 'Buttons',
            path: '/button',
          },
          {
            name: 'Images',
            path: '/image',
          },
          {
            name: 'Alerts',
            path: '/alerts',
          },
          {
            name: 'Animations',
            path: '/animations',
          },
          {
            name: 'Icons',
            path: '/icons',
          },
          {
            name: 'Editors',
            path: '/editor',
          },
          {
            name: 'Modal',
            path: '/modal',
          },
          {
            name: 'Mask',
            path: '/mask',
          },
        ],
      },
      {
        name: 'Page',
        icon: 'BookOutlined',
        path: '/page',
        children: [
          {
            name: 'Login',
            path: '/sign/login',
          },
          {
            name: 'Register',
            path: '/sign/register',
          },
          {
            name: 'Lock',
            path: '/lock',
          },
          {
            name: 'Gallery',
            path: '/gallery',
          },
          {
            name: 'Blank',
            path: '/blank',
          },
          {
            name: 'Result',
            path: '/result',
          },
          {
            name: 'Coming Soon',
            path: '/coming',
          },
          {
            name: '403',
            path: '/403',
          },
          {
            name: '404',
            path: '/404',
          },
          {
            name: '500',
            path: '/500',
          },
          {
            name: '',
            path: '/level-route/:sub?',
          },
        ],
      },
      {
        name: 'Business',
        icon: 'BulbOutlined',
        path: '/business',
        children: [
          {
            name: 'CRUD',
            path: '/crud/:detail?',
          }
        ],
      },
    ], 400)
  } 
}